import React from "react";
import "./Education.css";
import mongoDB from "./img/mongoDB.png";
import java from "./img/java.jpg";
import hcj from "./img/HCJ.png";
import react from "./img/react.png";
import sap from "./img/SAP.png";
import nodejs from "./img/Nodejs.png";
import premierePro from "./img/premiere-pro.jpg";
function Skills() {
    return(
        <div className="service component__space" id="Skills">
         <div className="heading">
            <h1 className="heading" >SKILLS</h1>
            <div className="img">
             <img src={hcj} height={"200px"} width={"600px"}></img>
             <img  src={react} height={"200px"} width={"200px"}></img>
             <img src={mongoDB} height={"180px"} width={"400px"}></img>
             <img src={java} height={"200px"} width={"200px"}></img>
             <img src={nodejs} height={"250px"} width={"300px"}></img>
             <img src={sap} height={"200px"} width={"400px"}></img>
             <img src={premierePro} height={"250px"} width={"300px"}></img>
            </div>
         </div>
        </div>
    )
}

export default Skills;