import emailjs from "emailjs-com";
import React, { useRef } from 'react';
import './Contact.css';
import contactImg from "./img/about-9.jpg"
import Swal from 'sweetalert2'
function Contact() {
    const form = useRef();

        const sendEmail = (e) => {
            e.preventDefault();

            emailjs.sendForm('service_tgt82ml', 'template_am18avb', form.current, 'user_mDLiNFrcFjMpAjBfVq69B')

                .then((result) => {
                    console.log(result.text);
                    Swal.fire({
                    position: 'center',
                    icon: 'success',
                    title: 'Your Email Send Successfully',
                    showConfirmButton: false,
                    timer: 1500
                    })
                }, (error) => {
                    console.log(error.text);
                    Swal.fire({
                    position: 'center',
                    icon: 'error',
                    title: 'Oops...Something Went Wrong',
                    showConfirmButton: false,
                    timer: 1500
                    })
                });
            form.current.reset()
        };
        return (
            <div className="contact component__space" id="Contact">
                <form ref={form} onSubmit={sendEmail}>
                 <div className="row">
                    <div className="col__2">
                        <div className="contact__box">
                            <div className="contact__meta">
                                <h1 className="hire__text">Contact Me.</h1>
                                <p className="hire__text white">I am available for freelance work. Connect with me via phone:</p>
                                <p className="hire__text white"><strong>(+91) 63823 76146</strong> or email <strong>muthupandi4600@gmail.com</strong></p>
                            </div>
                            <div className="input__box">
                                <input type="text" className="contact name"  name="Your Name" placeholder="Your name *" />
                                <input type="text" className="contact email" name="Your Email" placeholder="Your Email *" />
                                <input type="text" className="contact subject"  name="Your Subject" placeholder="Write a Subject" />
                                <textarea name="message" id="message"  name="Your Message" placeholder="Write Your message"></textarea>
                                <button className="btn contact pointer" onClick={Contact} type="submit">Submit</button>
                            </div>
                        </div>
                    </div>
                    <div className="col__2">
                        <img src={contactImg} height={"500"} width={"800"} alt="" className="contact__img" />
                        </div>
                </div>
             </form>
           </div>     
        
            )
    
}

export default Contact;
